import React, { Component, useState, useEffect, Fragment } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
// import usePortal from 'react-cool-portal';
import { component } from 'picoapp';
import { ReactDrawer } from '../../parts';
import CartList from './CartList';

const getCartData = async () => {
  const response = await axios.get('/cart.js', { headers: { Pragma: 'no-cache' } });
  return response.data;
};

const MiniCartButton = ({ handleClick, open, itemCount }) => {
  return (
    <button
      className="Button Button--minicart"
      data-layout="noLayout"
      onClick={() => handleClick(!open)}
      aria-controls="miniCartDrawer"
      aria-haspopup="true"
    >
      <svg xmlns="http://www.w3.org/2000/svg" width="500" height="500" viewBox="0 0 500 500" className="icon icon--primary">
        <g fill="none" fillRule="evenodd" stroke="none" strokeWidth="1">
          <g fill="currentColor" fillRule="nonzero" transform="translate(-1499 -502)">
            <path d="M1880.51 651.444h-51.796v-23.11c0-42.667-34.532-77.334-77.032-77.334s-77.031 34.667-77.031 77.333v23.111h-51.797c-4.87 0-8.854 4-8.854 8.89V942.11c0 4.889 3.984 8.889 8.854 8.889h257.656c4.87 0 8.855-4 8.855-8.889V660.333c0-4.889-3.985-8.889-8.855-8.889zm-188.15-23.11c0-32.89 26.562-59.556 59.322-59.556 32.76 0 59.323 26.666 59.323 59.555v23.111H1692.36v-23.11zm179.296 304.888h-239.948v-264h42.943v18.222c0 4.89 3.984 8.89 8.854 8.89 4.87 0 8.854-4 8.854-8.89v-18.222h118.646v18.222c0 4.89 3.985 8.89 8.854 8.89 4.87 0 8.855-4 8.855-8.89v-18.222h42.942v264z"></path>
          </g>
        </g>
      </svg>
      {itemCount > 0 && (
        <span className="counter">
          <span>{itemCount}</span>
        </span>
      )}
    </button>
  );
};

const MiniCart = ({ node, ctx }) => {
  const [open, setOpen] = useState(false);
  const [cart, setCart] = useState({ items: [] });
  const [user, setUser] = useState({});

  const updateLineItemQty = async (variant, qty) => {
    await axios
      .post('/cart/update.js', {
        updates: { [variant]: qty },
      })
      .then((response) => {
        setCart(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };
  const API = {
    updateLineItemQty,
  };

  // Update the Cart Data on mounting
  useEffect(() => {
    getCartData().then((data) => {
      setCart(data);
      ctx.emit('cartUpdate', { cart: data });
    });
  }, []);

  // Update cart and open when adding to cart
  useEffect(() => {
    ctx.on('cart.productAdded', () => {
      getCartData().then((data) => {
        setCart(data);
        setOpen(true);
        document.getElementById('shopify-section-site-header').scrollIntoView();
      });
    });
  }, []);

  return (
    <Fragment>
      <MiniCartButton handleClick={setOpen} open={open} itemCount={cart.item_count}></MiniCartButton>
      <ReactDrawer id="miniCartDrawer" showDrawer={open} setShowDrawer={setOpen} position={'right'}>
        <header className="Drawer__header">
          <h3>Your Cart</h3>
          <button type="button" className="Button" data-style="navy outline" data-layout="small" onClick={() => setOpen(!open)}>
            Close
          </button>
        </header>
        <CartList cart={cart} products={cart.items} API={API} />
      </ReactDrawer>
    </Fragment>
  );
};

export default component((node, ctx) => {
  const element = <MiniCart ctx={ctx} parentNode={node} />;
  ReactDOM.render(element, node);

  // Unmount component on Pjax navigation
  return function () {
    try {
      ReactDOM.unmountComponentAtNode(node);
    } catch (e) {
      console.error(e);
    }
  };
});
