import { component } from 'picoapp';
import { gsap } from 'gsap';
import { str2json } from '../../utilities';
// import { gsap } from 'gsap/all';
// import { disableBodyScroll, enableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock';

class Drawer {
  constructor(el, ctx) {
    this.isActive = false;
    this.ctx = ctx;
    this.identifier = el.dataset.identifier;
    this.init(el);
    this.initAnimations();
    this.bindEvents();
    this.initialized = true;
  }

  init(el) {
    this.el = el;
    // cache elements
    this.body = document.querySelector('body');
    this.drawer = el;
    this.drawerBox = el.querySelector('.Drawer__inner');
    this.backdrop = el.querySelector('.backdrop');

    const defaults = {
      position: 'left',
    };

    const instanceOptions = str2json(this.el.dataset.options);
    const options = { ...defaults, ...instanceOptions };
  }

  initAnimations() {
    const body = document.body;
    const wrapper = this.el;
    const backdrop = this.backdrop;
    const drawer = this.drawerBox;
    // const drawerSections = Array.from(this.drawerBox.children);

    this.tl = new gsap.timeline({
      // onStart: () => {
      //   //   console.log(wrapper);
      // },
      paused: true,
      onComplete: () => {
        body.classList.add('has-modalOpen');
        this.isActive = true;
      },
      onReverseComplete: () => {
        this.isActive = false;
        body.classList.remove('has-modalOpen');
      },
    });

    this.tl.set(wrapper, { display: 'flex', immediateRender: false });

    // Shade
    this.tl.from(wrapper, { opacity: 0, duration: 0.4 });
    this.tl.from(backdrop, {
      autoAlpha: 0,
      duration: 0.35,
    });

    // this.tl.set(drawer, { autoAlpha: 0, immediateRender: false });
    this.tl.from(
      drawer,
      {
        x: '-100%',
        autoAlpha: 1,
        duration: 0.5,
        ease: 'power3.out',
      },
      '-=0.4'
    );
  }

  bindEvents() {
    this.ctx.on('Drawer', (state) => {
      if (state.identifier == this.identifier) {
        if (typeof this[state.action] === 'function') {
          this[state.action]();
        }
      }
    });

    if (this.backdrop) {
      this.backdrop.addEventListener('click', (e) => {
        this.close();
      });
    }

    // Close modal with ESC key
    document.addEventListener('keyup', (e) => {
      if (e.key == 'Escape') {
        this.close();
      }
    });
  }

  toggle() {
    // Bailout if toggle component is disabled
    if (this.disabled) {
      return;
    }
    if (this.isActive) {
      this.close();
    } else {
      this.open();
    }
  }

  open() {
    const isActive = new CustomEvent('isActive');
    if (this.isActive) {
      return;
    }
    this.tl.play();
    this.el.dispatchEvent(isActive);
    document.querySelector('body').style.overflow = 'hidden';
  }

  close() {
    const isInactive = new CustomEvent('isInactive');
    if (!this.isActive) {
      return;
    }
    this.tl.reverse();
    this.el.dispatchEvent(isInactive);
    document.querySelector('body').removeAttribute('style');
  }
}

export default component((node, ctx) => {
  new Drawer(node, ctx);
});
