import { component } from 'picoapp';
var hoverintent = require('hoverintent');
import { gsap } from 'gsap';

class DesktopNav {
  constructor(el, ctx) {
    this.ctx = ctx;
    this.mainOptions = el.querySelectorAll('.Nav__item.level-1.has-submenu');
    this.mainOptions.forEach((item) => {
      let submenu = item.querySelector('.Nav__submenu.level-2');
      hoverintent(
        item,
        () => {
          item.classList.add('is-hover');
          gsap.set(submenu, { display: 'block', immediateRender: false });
          gsap.fromTo(submenu, { y: 10, opacity: 0 }, { y: 0, opacity: 1, duration: 0.3 });
          // console.log('enter menu item');
        },
        () => {
          item.classList.remove('is-hover');
          gsap.fromTo(
            submenu,
            { y: 0, opacity: 1 },
            {
              y: 20,
              opacity: 0,
              duration: 0.3,
              onComplete: () => {
                gsap.set(submenu, { display: 'none' });
              },
            }
          );
        }
      );
    });
  }
}

export default component((node, ctx) => {
  new DesktopNav(node, ctx);
});
