import React, { Component, useState, useEffect, Fragment } from 'react';
import ReactDOM from 'react-dom';
import { component } from 'picoapp';
import { ReactDrawer } from '../parts';
import { useMount } from '../../hooks';
import { motion, AnimatePresence } from 'framer-motion';
import classnames from 'classnames';
import { slugify } from '../../utilities';
import { ResponsiveImage } from '../parts/ResponsiveImage';

const ProductCard = ({ product }) => {
  return (
    <div className="Card" data-layout="vertical media-content">
      <figure className="Card__media">
        <ResponsiveImage image={product.featured_image}></ResponsiveImage>
      </figure>
      <div className="Card__details">
        <h4 className="Card__title">
          <a href={`/products/${product.handle}`}>{product.title}</a>
        </h4>
      </div>
    </div>
  );
};

const MenuItem = ({ link, level, showImage, path }) => {
  const [isOpen, setIsOpen] = useState(false);
  const optionClasses = classnames('Nav__item', `level-${level}`, {
    'is-active': link.active,
  });

  const handleClick = (e) => {
    if (link.links && level < 2) {
      e.preventDefault();
      setIsOpen(!isOpen);
    }
  };

  return (
    <div className={optionClasses}>
      <motion.a initial={false} onClick={handleClick} href={link.url} className={`Nav__link level-${level}`}>
        {showImage && level > 2 && (
          <figure>
            <img src={`${window.__webpack_public_path__}${path}.jpg`} alt={link.title} loading="lazy" />
          </figure>
        )}
        <span>{link.title}</span>
      </motion.a>
      {link.links && (
        <AnimatePresence initial={false}>
          {isOpen && (
            <motion.div
              key="content"
              initial="collapsed"
              animate="open"
              exit="collapsed"
              variants={{
                open: { opacity: 1, height: 'auto' },
                collapsed: { opacity: 0, height: 0 },
              }}
              transition={{ duration: 0.6, ease: [0.04, 0.62, 0.23, 0.98] }}
            >
              {level < 2 &&
                <SubMenu items={link.links} parent={link} level={level} showImage={showImage} path={path}></SubMenu>
              }
            </motion.div>
          )}
        </AnimatePresence>
      )}
    </div>
  );
};

const SubMenu = ({ items, level, parent, showImage, path }) => {
  return (
    <div className={`Nav__submenu level-${level}`} data-identifier={parent.object_handle}>
      {items.map((link, index) => {
        return (
          <MenuItem link={link} level={level + 1} key={link.title} showImage={showImage} path={`${path}_${slugify(link.title)}`}></MenuItem>
        );
      })}
    </div>
  );
};

const MobileNav = ({ parentNode }) => {
  const [showDrawer, setShowDrawer] = useState(false);
  const [mainMenu, setMainMenu] = useState({ title: '', links: [] });
  const [secMenu, setSecMenu] = useState({ title: '', links: [] });
  const [bestSellersMenu, setBestSellersMenu] = useState([]);
  const [customer, setCustomer] = useState();

  useMount(() => {
    let contentTag = parentNode.nextElementSibling;
    try {
      let menuData = JSON.parse(contentTag.innerText);

      // main menu
      let menuKey = Object.keys(menuData['main'])[0];
      setMainMenu(menuData['main'][menuKey]);

      // sec menu
      let menuSecKey = Object.keys(menuData['secondary'])[0];
      setSecMenu(menuData['secondary'][menuSecKey]);

      // Best Sellers
      setBestSellersMenu(menuData['products']);

      // customer
      // setCustomer(menuData['customer']);
    } catch (error) {
      console.log(error);
    }
  });

  const { links } = mainMenu;

  return (
    <>
      <button className="Button mobile-toggler" data-layout="noLayout" onClick={() => setShowDrawer(!showDrawer)}>
        <svg className="icon icon--primary" height="1em" viewBox="0 0 20 20" width="1em">
          <use xlinkHref="#menu"></use>
        </svg>
      </button>
      <ReactDrawer className="mobileNav" showDrawer={showDrawer} setShowDrawer={setShowDrawer} position={'left'}>
        <div className="Nav Nav--mainMobile Nav--vertical">
          {links.map((link, index) => {
            return <MenuItem link={link} level={1} key={link.title} showImage={link.title === 'Bags'} path={link.object_handle}></MenuItem>;
          })}
        </div>
        <div className="Nav Nav--secMobile Nav--vertical">
          {secMenu.links.map((link, index) => {
            return <MenuItem link={link} level={1} key={link.title} showImage={link.title === 'Bags'} path={link.object_handle}></MenuItem>;
          })}
        </div>
        {/* <div className="Nav Nav--account Nav--vertical">
          {customer ? (
            <>
              <div className="Nav__item">Hello {customer}</div>
              <a href="/account" className="Menu__item">
                My Account
              </a>
              <a href="/account/logout" className="Menu__item">
                Logout
              </a>
            </>
          ) : (
            <a href="/account/login" className="Menu__item">
              Login
            </a>
          )}
        </div> */}
        {bestSellersMenu.length > 0 && (
          <div className="Nav__bestSellers">
            <h4>Best Sellers</h4>
            <div className="items">
              {bestSellersMenu.map((item, index) => {
                return <ProductCard product={item} key={item.id}></ProductCard>;
              })}
            </div>
          </div>
        )}
        {/* <svg viewBox="0 0 40 20" className="icon logo-condensed" width="2em" height="1em">
          <use href="#logo-condensed"></use>
        </svg> */}
      </ReactDrawer>
    </>
  );
};

export default component((node, ctx) => {
  const element = <MobileNav ctx={ctx} parentNode={node} />;
  ReactDOM.render(element, node);
});
