require('object-fit-polyfill');
require('respimg-polyfill');
require('bgset');
const lazysizes = require('lazysizes');
const delegate = require('delegate');
import { str2json } from '../utilities';

export default {
  init: function (app) {
    this.app = app;

    delegate(
      document.body,
      '[data-action]',
      'click',
      (e) => {
        e.preventDefault();
        const evData = str2json(e.delegateTarget.dataset.action);
        this.app.emit(evData.type, { identifier: evData.target, action: evData.action });
      },
      false
    );

    document.querySelectorAll('[data-action]').forEach((item) => {
      item.addEventListener('keydown', (e) => {
        if (e.keyCode === 13) {
          const evData = str2json(item.dataset.action);
          this.app.emit(evData.type, { identifier: evData.target, action: evData.action });
        }
      });
    });

    // delegate(
    //   document.body,
    //   '[data-action]',
    //   'onkeydown',
    //   (e) => {
    //     console.log('has focused');
    //     e.preventDefault();
    //     const evData = str2json(e.delegateTarget.dataset.action);
    //     this.app.emit(evData.type, { identifier: evData.target, action: evData.action });
    //   },
    //   false
    // );

    if (!navigator.cookieEnabled) {
      document.body.classList.add('cookies-not-supported');
    }
  },
};
